#workouts {
	padding-bottom: 71px;
	min-width: 100%;
	position: absolute;
	top: 0;
}

.workouts-container {
	display: flex;
	flex-wrap: wrap;
	justify-content: flex-start;
	margin: 0 10px;
}

#workouts h1.fadeIn {
	color: white;
	animation: 2s fadeIn ease-in 1s;
	animation-fill-mode: forwards;
	margin: 10px 0 0;
}

@keyframes fadeIn {
	from {
		opacity: 0;
	}
	to {
		opacity: 1;
	}
}
